export default function addDownloadButton() {
    const btns = document.getElementsByClassName("dlbtn");
    const last = btns[btns.length - 1];
    let all = last.cloneNode(true);
    all.removeAttribute("href");
    all.removeAttribute("target");
    all.id = "download-all-mods";
    let span = all.getElementsByTagName("span")[0];
    span.innerHTML = "Download All Mods" + span.innerHTML.substring(span.innerHTML.indexOf('<'));
    all.addEventListener("click", downloadAllMods);
    last.parentElement?.appendChild(all);
}
function downloadAllMods() {
    console.log("Initiate all-mods download");
    const li = document.getElementsByClassName("download-link-cache");
    for (let i = 0; i < li.length; i++) {
        const link = li[i];
        //if (btn.hasAttribute("href"))
        //	window.open(btn.getAttribute("href") as string);
        window.open(link.innerHTML);
    }
}
